form{
  /* width: 80%; */
  margin-inline: auto;
  padding-block: 2rem;
  text-align: center;
  position: relative;
}
form input {
  padding: .5rem .7rem;
  background-color: #eee;
  color: #333;
  width: 100%;
  border-radius: .2rem;
  margin-right: 1.5rem;
}
form button{
  border: 1px solid #222;
  padding: 5px 8px;
  border-radius: .2rem;
  font-weight: bold;
  position: absolute;
  right: 3px;
  top: 36px;
}
form button:active{
  scale: .90;
}
:focus{
  outline: none;
}